//    -- anjam upload doc
//    -- sabt etelat  ezafi assistant

import React, {memo, useState, useEffect} from 'react';
// library estefadeh shode baray traslation
import {useTranslation} from "react-i18next";
// library jquery
import $ from 'jquery';
// library materilize bary estefade az componenthai materilize
import M from 'materialize-css';
// dispatch , use selector functional bary etesalat action va reducer
import {useDispatch, connect, useSelector} from "react-redux";
// fitdata baray sabt etelat form data
import {FitDataAssistant ,UploadedFile} from '../../../../../entity/Redux/Auth/SignUp/Action';
// library ertebat ba api
import axios from "axios";
import {SECRET_USER_DATA} from "../../../../../services/Authentication/Types";
import {Link} from "react-router-dom";
import {Redirect} from "react-router";


// functional component upload file
export default memo((props) => {
        // function translte
        const {t, i18n} = useTranslation();
        // import dispatcher
        const dispatch = useDispatch();
        // state data user
        const [dataUser, setDataUser] = useState({});
        // state selctfile brary file
        const [selectFile, setSelectFile] = useState();
        // daryaft response dispatch form
        const respo = useSelector(state => state.Auth);
        // path monseb baray redirect page ( doctor or assistant)
        const [pathhis, setPathhis] = useState({path: '/doctor'});

        let btnSave = '<button className="btn blue darken-4 waves-effect" onClick={e => submitUserDetail(e)} id="check_phone_num"> {t("Save.l")}</button>';
        let btnSaveDisable = '<button className="btn blue darken-4 waves-effect" disabled id="check_phone_num"> {t("Save.l")}</button>';

        // ejra bad az load component
        useEffect(() => {
            {/*
        -- assistance check
        -- check kardan assistance boodan ya doctor boodan jahat namayesh filedhai ezafi etelaat assistance
        -- check be vaisle checkbox : checked
    */
            }

            var elems = document.querySelectorAll('select');
            M.FormSelect.init(elems);

            {/*
           --  reducer response
           --  in bakhsh bad az dispatch shodan etelaat form anjam mishavad
           --  status code ro check mikone va return monaseb ro mideh
        */
            }

            if (respo) {
                console.log("respo : ", respo)
                // check kardan response uplaod file
                if (respo.resLog.data) {
                    if (respo.resLog.data.success) {

                        // window.location.pathname = pathhis.path; // rediret to path monaseb
                    }
                }
            }

            //  end reducer response
        })

// set kardan file uploady dar state baray ersal be cdn
        const changeHandlerFile = (event) => {
            setSelectFile(event.target.files[0]);
        }


        {/*
    -- language
    -- do zabaneh kardan site
    */
        }

        function handleLang(e) {
            // e.preventDefault();
            // sabt type language dar storage
            if (!localStorage.getItem("lan")) {
                localStorage.setItem("lan", 'en');
            }
            //daryaft type language as localstorage
            var lan = localStorage.getItem("lan");
            var ckeckbox = $("input[name=cahngelan]");
            if (ckeckbox.is(":checked") && lan === 'en') {
                localStorage.setItem("lan", 'ch');
            } else {
                localStorage.setItem("lan", 'en');
            }
            let lang = localStorage.getItem('lan');
            // gharar dadan language dar library translate
            i18n.changeLanguage(lang);
        }

// end language


        {/*
        -- submit form
        -- submit kardan form marboot be eteleat
    */
        }

        let btn_save = ''


        const [userData, setUserData] = useState();


        function funcUserData(e) {
            setFIlePath({
                ...filePath,
                [e.target.name]: e.target.value
            })
        }


        const [filePath, setFIlePath] = useState({isAssistant: true});

        function upload_file() {
            const formData = new FormData(); // new kardan formData
            $("#loading_back").fadeIn(); // fadeIn backLoading
            // if (selectFile) {
            // check konim ke file add shode ya na
            formData.append("file", selectFile); // append kardan filename be formdata
        
            $("#check_phone_num").fadeIn();
            setTimeout(() => {
              dispatch(UploadedFile(formData));
            }, 300);
          }


        function submitUserDetail() {
            // check kardan inke karbar mikhad phoneNumber befreste ya na
            if (filePath.phoneNumber && filePath.phoneNumber !== '') {
                // check kardan shekl true phoneNumber
                const regexs = /^(?:\+?86)?1(?:3\d{3}|5[^4\D]\d{2}|8\d{3}|7(?:[0-35-9]\d{2}|4(?:0\d|1[0-2]|9\d))|9[0-35-9]\d{2}|6[2567]\d{2}|4(?:(?:10|4[01])\d{3}|[68]\d{4}|[579]\d{2}))\d{6}$/;
                if (regexs.test(filePath.phoneNumber)) {
                    // save etelaat assistant dar storage baray merge kardan ba list doctors ke mikhad
                    // darkhast barashun befresteh
                    localStorage.setItem(SECRET_USER_DATA + "dataUser", JSON.stringify(filePath));
                    // namayesh toast jahat anjam success job
                    M.toast({html: 'Please wait to be taken to the doctor selection page'})
                    setTimeout(() => {
                        // tagheir route be doctor-search
                        window.location.pathname = "/doctor-search"
                    }, 1500)
                } else {
                    M.toast({html: 'Please enter the phone number correctly'})
                }
            } else {
                localStorage.setItem(SECRET_USER_DATA + "dataUser", JSON.stringify(filePath));
                console.log("data : ", JSON.stringify(filePath))
                M.toast({html: 'Please wait to be taken to the doctor selection page'})
            }


            // dispatch(FitDataAssistant(filePath))
        }

        // end submit form

        const select_type = t('select-type.l');
        return (
            <div className="auth_back">
                <div id="back_shado"></div>

                <div className="container">
                    <div className="col s12">
                        <div className="row">
                            <div className="col l4 m2 s1">

                            </div>
                            <div className="col l4 m8 s10">

                                {/*
                                --
                            */}

                                <div id="auth_box">
                                    <div id="change_or_sub">
                                        <input type="checkbox" name="cahngelan" onChange={handleLang}
                                               id="change_lang_login"/>
                                        <span>{t('Lang.l')}</span>
                                        {/*<span></span>*/}
                                    </div>

                                    <div className={`mini_box_over`}>
                                        <div id={`mini_circule`}>
                                            <i className="material-icons">person_outline</i>
                                        </div>
                                    </div>
                                    <div className={`mini_box_over`} style={{right: 0}}>
                                        <div id={`mini_circule_r`}>
                                            <img src="/img/icons/doctorB.png"/>
                                        </div>
                                    </div>
                                    <div className="container">
                                        <div className="row">

                                            <div className="row">
                                                <div className="input-field col s12" style={{marginTop: 55}}>
                                                    <p id="text_sub">{t('Welcome-to.l')}</p>
                                                    <p id="text_sub" style={{
                                                        fontSize: "40px",
                                                        marginTop: "15px !important",
                                                        marginBottom: "15px !important"
                                                    }}>Bi Con</p>
                                                    <p id="text_sub">{t('UserRegData.l')}</p>
                                                </div>
                                            </div>

                                            <div className="register_type_user" id="verifyBorder"
                                                 style={{hight: "auto"}}>


                                                <div id="loading_back">


                                                    <div className="preloader-wrapper big active" id="loading_progress">
                                                        <div className="spinner-layer spinner-blue-only">
                                                            <div className="circle-clipper left">
                                                                <div className="circle"></div>
                                                            </div>
                                                            <div className="gap-patch">
                                                                <div className="circle"></div>
                                                            </div>
                                                            <div className="circle-clipper right">
                                                                <div className="circle"></div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>


                                                {/*

                                                -- data form
                                                -- form vorod etelaat

                                            */}

                                                <div className="container">
                                                    <div className="row">
                                                        <div className="col s12">
                                                            <div className="row">

                                                            </div>
                                                            <div className="file-field input-field"
                                                                 style={{position: "relative"}}>
                                                                <div className="btn"
                                                                     style={{backgroundColor: '#00b8d4 !important'}}>
                                                                <span><i
                                                                    className="material-icons">attachment  </i></span>
                                                                    <input
                                                                        onChange={e => changeHandlerFile(e)}
                                                                        type="file"
                                                                        name="file"/>
                                                                </div>

                                                                <div className="file-path-wrapper">
                                                                    <input className="file-path validate"
                                                                           type="text"
                                                                           placeholder="Upload one or more files"/>
                                                                </div>

                                                                <div id={"progresss"}
                                                                     onClick={e => selectFile ? upload_file(e) : M.toast({html: 'please select file ...'})}>
                                                                    <span>
                                                                        <i className={"material-icons"}>upload</i>
                                                                    </span>
                                                                </div>

                                                            </div>


                                                            <div className="input-field col s12"
                                                                 style={{marginTop: 0}}>
                                                                <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('phoneNumber.l')}</p>
                                                                <div id="input_back">
                                                                    <input placeholder={t('U-phone-num.l')}
                                                                           name="phoneNumber"
                                                                           onChange={e => funcUserData(e)}
                                                                           autoComplete="false"
                                                                           type="text" className="validate"/>
                                                                </div>
                                                                {/*<p id="alert-p">&nbsp;&nbsp;{t('must-field.l')}</p>*/}

                                                                {/*<label htmlFor="password">Password</label>*/}
                                                            </div>

                                                            {/*  input marbout be description assistant  */}
                                                            <div className="input-field col s12"
                                                                 style={{marginTop: 10}}>
                                                                <p style={{color: '#ccc'}}>&nbsp;&nbsp;{t('ass-description.l')}</p>
                                                                <div id="input_back">
                                                                            <textarea
                                                                                onChange={e => funcUserData(e)}
                                                                                placeholder={t('U-ass-description.l')}
                                                                                name="description"
                                                                                autoComplete="false"
                                                                                className="validate"/>
                                                                </div>
                                                            </div>


                                                            <div className="col s12" style={{marginBottom: 20}}>
                                                                <Link to={"/upload-file"}>
                                                                    <p id={"assistant-re"}>
                                                                        <span id={"assistant"}><i
                                                                            className={"material-icons"}
                                                                            style={{color: "#ccc !important"}}>keyboard_arrow_left</i></span>
                                                                        <span id={"i-am-assis"} style={{marginLeft: 15}}>I am a doctor</span>

                                                                    </p>
                                                                </Link>
                                                            </div>


                                                            {/* btn save baray form*/}
                                                            <div className={`col s12`}
                                                                 style={{marginBottom: 30, marginTop: 30}}>
                                                                <button className="btn blue darken-4 waves-effect"
                                                                        onClick={e => submitUserDetail(e)}
                                                                        id="check_phone_num"> Next
                                                                </button>
                                                                {/*</Link>*/}
                                                            </div>
                                                        </div>

                                                    </div>
                                                </div>


                                                {/*        end data form     */}
                                            </div>
                                        </div>
                                    </div>

                                </div>


                            </div>
                            <div className="col l4 m2 s1">

                            </div>
                        </div>

                    </div>

                </div>


            </div>
        );
    }
)
